footer {
    /* border: 1px solid rgb(255, 0, 0); */
    width: 100%;
    flex-grow: 1;
    display:  flex;
    justify-content: center;
    align-items: center;

    opacity: 0%;

    transition: opacity 1.5s;
}

.hide-footer {
    opacity: 0%;
}

.show-footer {
    opacity: 100%;
}

.container_footer {
    /* border: 1px solid rgb(255, 0, 0); */
    width: 1100px;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.content_footer {
    /* border: 1px solid black; */
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    bottom: 0px;
    transition: bottom 0.6s;
}

.up_scroll {
    bottom: 0px;
}

.down_scroll {
    bottom: 500px;
}



.container_address_in_footer {
    /* border: 1px solid black; */
    width: 500px;
    height: 470px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container_form_in_footer {
    /* border: 1px solid black; */
    width: 500px;
    min-height: 470px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.address_in_footer {
    /* border:1px solid black; */
    width: 100%;
    height: 469px;
}

.address_in_footer > .container_title {
    /* border: 1px solid black; */
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.address_in_footer > .container_title > .title {
    /* border: 1px solid black; */
    max-width: 400px;
    height: 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 3rem;
    text-align: center;
    line-height: 97px;
    color: #bba78c;
}

.address_in_footer > .container_text {
    /* border: 1px solid black; */
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.address_in_footer > .container_text > .text {
    /* border: 1px solid black; */
    max-width: 450px;
    /* height: 170px; */
    font-family: Arial, Helvetica, sans-serif;
    text-align: justify;
    text-align-last: center;
    color: #bba78ca6;
    font-weight: bold;
}

.address_in_footer > .container_items  {
    /* border: 1px solid black; */
    width: 100%;
    height: 270px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.address_in_footer > .container_items > .items {
    /* border: 1px solid black; */
    max-width: 450px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.address_in_footer >  .container_items > .items > .item {
    /* border: 1px solid black; */
    max-width: 350px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.address_title {
    /* border: 1px solid black; */
    width: 100%;
    height: 50%;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    color: #bba78c;
    line-height: 40px;
    font-weight: bold;
    font-size: 18px;
    letter-spacing: 1px;
}

.address_content {
    /* border: 1px solid black; */
    width: 100%;
    height: 50%;
    font-family: Arial, Helvetica, sans-serif;
    color: #bba78ca6;
    text-align: center;
    line-height: 40px;
    font-weight: bold;
}

/*form*/

.container_form_in_footer {
    border-radius: 5px;
    background-image: radial-gradient(circle , #8d4f00, #593200);
    box-shadow: -7px 8px 15px 0px rgba(0, 0, 0, 0.438);
}

.title_form_in_footer > .title {
    max-width: 400px;
    height: 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 3rem;
    text-align: center;
    line-height: 97px;
    color: #bba78c;
}

.form_in_footer {
    /* border: 1px solid black; */
    width: 100%;
    height: 370px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container_inputs {
    /* border: 1px solid black; */
    max-width: 400px;
    height: 310px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input_item {
    border-bottom: 3px solid #bba78c;
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.input_item > label {
    /* border: 1px solid black; */
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-family: Arial, Helvetica, sans-serif;
    color: #bba78c;
}

.input_item > input {
    height: 50px;
    max-width: 310px;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    color: #bba78c;
    border: none;
    outline: none;
    text-indent: 5px;
    background-color: #c9720000;
}

.container_btn {
    /* border: 1px solid black; */
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container_btn > button {
    width: 200px;
    height: 40px;
    background-color: #c9720000;
    color: #bba78c;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    border: none;
}

@media (max-width : 1103px) {
    .container_form_in_footer {
        background-image: none;
        box-shadow: none;
    }
}

@media (max-width : 1000px) {

    .container_footer {
        flex-direction: column;
        height:444px;
        overflow: hidden;
    }

    .content_footer {
        flex-direction: column;
    }
    
}

@media (max-width : 500px) {

    .container_address_in_footer  {
        width: 100%;
        height: auto;
        margin-bottom: 7px;
    }

    .container_form_in_footer {
        width: 100%;
        height: auto;
    }

    .text {
        width: 90%;
    }
}